<template>
  <div id="ManuaCode">
      <div class="imgDiv">
        <img src="../../assets/cars.png" alt="">
      </div>
      <ul class="inputCode">
      	<input type="text" v-model="inputValue"    class="inputValue"/>
        <li><input type="text" disabled="disabled" :value="value1"></li>
        <li><input type="text" disabled="disabled" :value="value2"></li>
        <li><input type="text" disabled="disabled" :value="value3"></li>
        <li><input type="text" disabled="disabled" :value="value4"></li>
        <li><input type="text" disabled="disabled" :value="value5"></li>
        <li><input type="text" disabled="disabled" :value="value6"></li>
        <li><input type="text" disabled="disabled" :value="value7"></li>
        <li><input type="text" disabled="disabled" :value="value8"></li>
        <li><input type="text" disabled="disabled" :value="value9"></li>
        <li><input type="text" disabled="disabled" :value="value10"></li>
        <li><input type="text" disabled="disabled" :value="value11"></li>
      </ul>
    <div class="remark ">请输入车辆的SN码</div>
    <p class="prompt">编号输入有误，请重新输入</p>
  </div>
</template>

<script>
  export default {
    name: "userName",
    data () {
      return {
      	 inputValue:'',
	       value1:"",
	       value2:"",
	       value3:"",
	       value4:"",
	       value5:"",
	       value6:"",
	       value7:"",
	       value8:"",
	       value9:"",
	       value10:"",
	       value11:""
      }
    },
    computed: {
    	listenCode() {
        return this.inputValue;
     }
  },
	  watch: {
	    listenCode(newVal, oldVal) {
	       for(var i=0;i<11;i++){
	       	   this["value"+(i+1)]=newVal.substr(i,1)
	       }
	    }
	  },
    methods:{

    }
  }
</script>

<style scoped  lang="scss"  >
#ManuaCode{
   height: 100%;
   width: 100%;
  background: url("../../assets/back2.png")no-repeat;
  background-size:100% 100% ;
}
.inputCode{
   height: 100px;
   width: 100%;
   margin-top: 32px;
  padding: 0 30px 0 18px;
  box-sizing: border-box;
  position: relative;
  display: flex;
  .inputValue{
  	 width: 100%;
  	  height: 100px;
  	  position: absolute;
  	  top: 0;
  	  left: 0;
  	  background: rgba(255,255,255,0);
  	  padding: 0 30px 0 10px;
			color:rgba(255,255,255,0);

			    letter-spacing:42px;
  }
  li{
	  display: flex;
	  padding-left: 12px;
	  height: 100%;
	  input{
	  	 width: 100%;
	  	background: url(../../assets/back3.png);
	  	background-size: 100% 100%;
	  	text-align: center;
	  			box-sizing: border-box;
  		font-size:32px;
			font-family:PingFangSC-Regular;
			font-weight:400;


	  }

	}
}

.header {
  text-align: center;
  color: #fff;
  position: relative;
  height: 88px;
}
.header  span{
  font-size:32px;
  font-family:PingFangSC-Medium;
  font-weight:500;
  color:rgba(255,255,255,1);
  line-height: 88px;
}
.header p{
   position: absolute;
   right: 40px;
  top: 0;
  line-height: 88px;
  font-size:28px;
  font-family:PingFangSC-Regular;
  font-weight:400;
  color:rgba(255,255,255,1);
}
.header i{
  content: "";
  position: absolute;
  top: 13px;
  left: 30px;
  margin: 20px;
  border-right: 4px solid #fff;
  border-bottom: 4px solid #fff;
  width: 18px;
  height: 18px;
  transform: matrix(-0.71,-0.71, -0.71, 0.71, 0, 0)
}
.imgDiv{
  width:420px;
  height:280px;
  margin: 0 auto;
  margin-top: 86px;
}
.imgDiv img{
  height: 100%;
  width:100%;
}
.remark{
  margin-top: 21px;
  height: 36px;
  font-size: 26px;
  text-align: center;
  width: 100%;
}
  .prompt{
    height: 34px;
    line-height: 34px;
    font-size: 24px;
    width: 100%;
    text-align: center;
    color: #FF5500;
    margin-top: 10px;
  }
</style>



